import React, { Component } from 'react'
import {  Steps, Timeline, Popover } from 'antd';
import { getAction } from '@/axios';
import moment from 'moment'

const auditStatusList = [
    {color:"#52C41A",text:""},
    {color:"red",text:""},
    {color:"#52C41A",text:""},
    {color:"orange",text:""}
]
function findStatus(text){
    let arr = auditStatusList.find(item => item.text===text);
    if(arr){
        return arr
    }
    return false;
}
const stepComponent = (stepsData = {current:0,setps:[]}) => {
    if(!stepsData.setps){
        return <div style={{textAlign:"center"}}></div>
    }
    return (
        <Steps current={stepsData.current} progressDot={true}>
            {
                stepsData.setps.map((item,index) => (
                    <Steps.Step key={index} title={item.title} description={
                        <Popover
                            placement="right"
                            content={
                                <Timeline>
                                    {
                                        item.toolTips?item.toolTips.map((el,index) => (
                                            <Timeline.Item key={index}>
                                                <p style={{color:"rgba(0,0,0,0.65)"}}>
                                                    {el.job}
                                                    <span style={{color:"#000000",padding:"0 5px"}}>{el.name}</span>
                                                    {
                                                        findStatus(el.status)?<span style={{color:findStatus(el.status).color}}>{findStatus(el.status).text}</span>:""
                                                    }
                                                </p>
                                                {
                                                    (el.status==='' || el.status==='')?
                                                    <>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}><span>{el.comments}</span></p>
                                                        <p style={{color:"rgba(0,0,0,0.65)"}}>{moment.unix(el.time/1000).format("YYYY/MM/DD HH:mm:ss")}</p>
                                                    </>:null
                                                } 
                                            </Timeline.Item>
                                        )):null
                                    }
                                </Timeline>
                            }
                        >
                            <span style={{cursor:"pointer",color:"#3F54A2"}}>{item.detail}{item.detail?item.approveType===1?'()':'()':''}</span>
                        </Popover>
                    } />
                ))
            }
        </Steps>
    )
}
export default class TaskDetail extends Component {
    state={
        steps:{//
            current:1,
            setps:[]
        }
    }
    componentDidMount() {
        this.getData()
    }
    getData=()=>{
        getAction(`/ybt/planTask/queryProcessMap`,{
            processId:this.props.currentTaskInfo.processId,
            taskId:this.props.currentTaskInfo.taskId
        }).then(res=>{
            if(res.success){
                this.setState({
                    steps:res.obj
                })
            }
        })
    }
    render() {
        return (
            <React.Fragment>
                {stepComponent(this.state.steps)}
            </React.Fragment>
        )
    }
}
