import React, { useState, useEffect } from 'react';
import { Table,message } from 'antd';
import { postAction } from '@/axios';
export default function AttachmentTable(props){
    const columns = [
        {
            title:"",
            dataIndex:"sortIndex",
            width:"20%"
        },{
            title:"",
            dataIndex:"name",
            width:"40%",
            render:text => (<span title={text}>{text}</span>)
        },{
            title:"",
            dataIndex:"type",
            width:"20%"
        }
    ];
    const [ dataSource, setDataource ] = useState([]);
    let getAttachmentRequest = (planTaskId) => {
        postAction("/ybt/planTask/getPlanAttachments",{planTaskId}).then(res => {
            if(res.success){
                setDataource(res.obj.map((item,index)=>({sortIndex:index+1,...item})))
            }else{
                message.error(res.obj)
            }
        })
    }
    useEffect(() => {
        getAttachmentRequest(props.currentTaskInfo.planTaskId)
      },[props.currentTaskInfo.planTaskId]);
    return (
        <Table
            rowKey="sortIndex"
            dataSource={dataSource}
            columns={columns}
        />
    )
}
