import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Input, DatePicker, Select, Button } from 'antd';
import moment from 'moment';
import styles from './index.module.less';

const { RangePicker } = DatePicker;

// const resultList = [//
//     {label:"",value:0},
//     {label:"",value:1},
//     {label:"",value:2},
// ];
// const taskTypeList = [//
//     {label:"",value:0},
//     {label:"",value:2},
//     {label:"",value:1},
// ];
const dateTypeList = [//
    {label:"",value:"reciveDate"},
    // {label:"",value:"auditDate"},
    {label:"",value:"completionDate"},
]
const statusList = [//
    {label:"",value:0},
    {label:"",value:4},
    {label:"",value:1},
    {label:"",value:2},
    {label:"",value:3},
    {label:"",value:5},
    {label:"",value:6},
    {label:"",value:8},
]

class FilterBox extends Component {
    constructor(props){
        super(props);
        this.state = {
            formName:"",//
            planTaskName:"",//
            // result:0,//
            // taskType:0,//
            taskStatus:0,//
            dateType:"reciveDate",//
            date:[],//
            taskCreOrDeptKeyword:""///
        }
    }
    componentDidMount(){
        document.addEventListener("keydown",this._onkeydown);
    }
    _onkeydown = (e) => {
        if(e.keyCode === 13){
            this.handleSearch();
        }
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this._onkeydown)
    }
    //
    handleChangeInput = (e,type) => {
        this.setState({
            [type]:e.target.value
        })
    }
    //
    handleChangeStatus = (value,type) => {
        this.setState({
            [type]:value
        })
    }
    //
    handleChangeDate = (dates,dateString) => {
        this.setState({
            date:dates
        })
    }
    //
    handleSearch = () => {
        const date = this.state.date.length>0?[moment(this.state.date[0]).format("YYYY-MM-DD HH:MM:SS"),moment(this.state.date[1]).format("YYYY-MM-DD HH:MM:SS")]:[];
        const filterValue = {
            ...this.state,
            [this.state.dateType]:date
        }
        delete filterValue.dateType;
        delete filterValue.date;
        //
        this.props.updateFilterValue(filterValue)
    }

    // 
    handleReset=()=>{
        this.setState({
            formName:"",//
            planTaskName:"",//
            // result:0,//
            // taskType:0,//
            taskStatus:0,//
            dateType:"reciveDate",//
            date:[],//
            taskCreOrDeptKeyword:""///
        },() => {
            this.handleSearch();
        })
        
    }

    render() {
        return (
            <div className={styles.filterBox}>
               <div className={styles.filterItem}>
                    
                    <Input style={{width:220}} placeholder="" value={this.state.formName} onChange={(e) => {this.handleChangeInput(e,"formName")}}/>
                </div>
                <div className={styles.filterItem}>
                    
                    <Input style={{width:220}} placeholder="" value={this.state.planTaskName} onChange={(e) => {this.handleChangeInput(e,"planTaskName")}}/>
                </div>
                {/* <div className={styles.filterItem}>
                    
                    <Select style={{width:120}} value={this.state.result} onChange={(val) => this.handleChangeStatus(val,"result")}>
                        {resultList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                </div> */}
                {/* <div className={styles.filterItem}>
                    
                    <Select style={{width:120}} value={this.state.taskType} onChange={(val) => this.handleChangeStatus(val,"taskType")}>
                        {taskTypeList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                </div> */}
                <div className={styles.filterItem}>
                    
                    <Select style={{width:120}} value={this.state.taskStatus} onChange={(val) => this.handleChangeStatus(val,"taskStatus")}>
                        {statusList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                </div>
                <div className={styles.filterItem}>
                    <Select style={{width:120}} value={this.state.dateType} onChange={(val) => this.handleChangeStatus(val,"dateType")}>
                        {dateTypeList.map((item) => (<Select.Option key={item.value} value={item.value}>{item.label}</Select.Option>))}
                    </Select>
                    <RangePicker style={{width:320}} value={this.state.date} format={"YYYY/MM/DD"} onChange={this.handleChangeDate}/>
                </div>
                <div className={styles.filterItem}>
                    /
                    <Input style={{width:220}} placeholder="" value={this.state.taskCreOrDeptKeyword} onChange={(e) => {this.handleChangeInput(e,"taskCreOrDeptKeyword")}}/>
                </div>
                <div className={styles.filterItem}>
                    <Button type='primary' ghost icon="search" style={{marginRight:12}} onClick={this.handleSearch}></Button>
                    <Button icon="sync" onClick={this.handleReset}></Button>
                </div>
            </div>
        )
    }
}

export default withRouter(FilterBox)